html {
  font-family: "Open Sans", sans-serif;
  background-color: #181818;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.text {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  align-items: stretch;
  color: white;
}

.nav {
  color: white;
  display: flex;
  align-items: stretch;
  gap: 30%;
  overflow: hidden;
  /*border: solid;
  border: #000000;
  border: 300px;*/
}

.site-title {
  /*font-size: 2rem;*/
  font-size:calc(30px + 2vw);
  color: white;
  text-decoration: none;
}

.nav ul {
  justify-content: center;
  padding-left: calc(5px + 1.2vw);
  margin: 20px;
  list-style: none;
  display: flex;
  gap: 5%;
}

.nav a {
  font-size:calc(5px + 2vw);
  color: inherit;
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 0.2rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border-left: 3px solid;
  border-right: 3px solid;
}

/*.nav a {
  display: flex;
  align-items: center;
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px;
  text-decoration: none;
}*/

.nav li.active {
  /*background-color: #222222;*/
  color: red;
}

.nav li:hover {
  /*background-color: #141414;*/
  color: rgb(141, 48, 48);
}

.nav .icon {
  display: none;
}

.content {
  padding: 16px;
}
/*
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.sticky + .content {
  padding-top: 60px;
}*/

.welcome-title {
  color: white;
  /*display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;*/
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.enter-button {
  background-color: rgba(255, 0, 0, 0.411);
  text-decoration: none;
  color: inherit;
  padding: 0.3rem;
  position: fixed;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.splash {
  background-image: url("../bokeh.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.project-container {
  display: flex;
  overflow: auto;
  justify-content: start;
  font-size: calc(5px + 1vw);;
  gap: 2%;
  color: whitesmoke;
  padding: calc(10px + 1.5vw);
  border-color: #300e0e;
  border-style: solid;
  border-top: 10px solid;
  border-bottom: 10px solid;
  background-color: #222222;
  width: 100%;
}

.archives {
  color: white;
  display: block;
  align-items: center;
  border-bottom: 10px;
}

.newest-project {
  font-size: calc(6px + 2vw);;
  overflow: auto;
  background-color: rgb(44, 0, 0);
  color: white;
  display: flex;
  align-items: center;
  gap: 5%;
  padding: calc(30px + 1w);;
  margin: 50px;
}

.centered-thing {
  text-align: center;
  color: white;
  margin-top: 10%;
}

.more-info-button {
  color: white;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.2rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

.more-info-button a {
  color: white;
  text-decoration: none;
}

.make-navbar-horizontal {
  background-color: #000000;
  display: flex;
  justify-content: space-evenly;
  gap: 5%;
  color: whitesmoke;
  padding: 40px;
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

.big-text{
  font-size:calc(20px + 1.3vw);
  margin: 2%;
}

